.donate-form {
  padding-bottom: 63px;
  
  @media (min-width: $medium-breakpoint) {
    padding: 0 115px 63px;
  }
  
  &__item {
    margin-bottom: 31px;    
    
    &:nth-last-child(2) {
      margin-bottom: 25px;
    }
    
    @media (min-width: $medium-breakpoint) {
      margin-bottom: 22px;
      position: relative;
      
      &--margin-bottom {
        margin-bottom: 31px;
      }
      
    }
    
    &--divider {
      padding-top: 32px;  
      position: relative;
      
      &:before {
        content: '';
        position: relative;
        display: block;
        top: -32px;
        left: 0;
        width: 100%;
        height: 2px;
        border-radius: 1px;
        background: rgba(0, 0, 0, .15);
      }
      
      @media (min-width: $medium-breakpoint) {
        padding-top: 57px;
        
        &:before {
          top: -40px;
        }
        
      }
      
    }
    
    &--flex {
      display: flex;
      justify-content: space-between;
    }   
    
  }
  
  &__title {
    font-family: $ptsans;
    font-weight: bold;
    font-size: 1rem;
    line-height: 1rem;
    color: $colder;
    
    &--hide-for-medium {
      
      @media (min-width: $medium-breakpoint) {
        display: none;
      }
      
    }
    
  }
  
  &__input-block {
    margin-top: 18px;
  }
  
  &__description {
    font-family: $ptsans;
    font-size: 13px;
    line-height: 17px;
    color: $colder;
    margin-top: 13px;
    
    @media (min-width: $medium-breakpoint) {
      line-height: 19px;
      font-size: 16px;
    }
    
  }
  
  &__count-block {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  &__checkbox-label {
    display: flex;
    align-items: center;
    cursor: pointer;
    
    @media (min-width: $medium-breakpoint) {
      display: none;
    }
    
  }
  
  &__checkbox {
    display: none;
  }
  
  &__checkbox:checked ~ &__checkbox-icon:before {
    opacity: 1;
  }
  
  &__checkbox-icon {
    display: block;
    height: 24px;
    width: 24px;
    box-sizing: border-box;
    border: 2px solid $colder;
    border-radius : 3px;
    margin-right: 12px;
    margin-top: 16px;
    text-align: center;
    position: relative;
    
    &:before {
      content: '';
      color: rgba(0, 0, 0, .5);
      line-height: 22px;
      background: url(icon-heard.svg);
      display: block;
      height: 12px;
      width: 14px;   
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%,-50%);
      opacity: 0;
    }
  }
  
  &__checkbox-description {
    font-family: $ptsans;
    font-size: 13px;
    line-height: 17px;
    color: $colder;
    margin-top: 16px;
    width: 90px;    
  }
  
  &__dropdown {
    display: none;
    
    @media (min-width: $medium-breakpoint) {
      display: block;
      margin-top: 18px;
      position: absolute;
      right: 0;
      top: 0;
      z-index: 2;
    }
    
  }
  
  &__card-box {
    display: flex;
    margin-top: 14px;
    padding-right: 10px;
    justify-content: space-between;
    
    @media (min-width: $medium-breakpoint) {
      margin-bottom: 38px;
    }
    
  }
  
  &__card-block:last-of-type {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    
    .donate-form__description {
     text-align: right;
    }
    
  }
  
  &__card-label {
    display: block;
  }
  
  &__card {
    display: none;
  }
  
  &__card:checked ~  &__card-text {
    color: $colder;
  }
  
  &__card-text {
    font-family: $ptsans;
    font-size: 0.85rem;
    line-height : 17px;  
    color: rgba(0, 0, 0, .5);
  }
  
  &__input-wrap {
    display: flex;
    
    .donate-form__input-block:first-of-type {
      margin-right: 8px;
      
      @media (min-width: $medium-breakpoint) {
        margin-right: 19px;
      }
      
    }
    
  }
  
  &__btn-wrap {
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    
    @media (min-width: $medium-breakpoint) {
      margin-top: 60px;
    }
    
  }
  
}